import React from 'react'
import logo from './logo.svg';
import './App.css';
import {useState} from 'react';

function App() {

  let post = '강남 우동 맛집';
  let [titles, changeTitle] = useState(['남자 신발', '강남 음식점', '파이썬']);
  //변수 와  state 차이 -> 변수가 변경되면 html에서도 바꿔줘야 한다.
  //하지만 state는 html 자동으로 재랜더링 됨.
  let [logo, setLogo] = useState('ReactBlog');
  let [like, changeLike] = useState([0, 0, 0]);
  
  let [modal, setModal] = useState(false);
  let [title, setTitle] = useState('0');
  let [input, setInput] = useState('');

  return (
    <div className = "App">
      <div className="black-nav">
        <h4>{logo}</h4>
      </div> 

      <button onClick = {()=> {
          let copy = [...titles];
          copy[0] = '여자 신발';
          changeTitle(copy);
      }}>글수정</button>
{/* 
      <div className="list">
        <h4>{titles[0]} <span onClick={ () => {changeLike(like+1)} }> 👍 </span> {like} </h4>
        <p>2월 10일 발행</p>
      </div>

      <div className="list">
        <h4>{titles[1]}</h4>
        <p>2월 12일 발행</p>
      </div>

      <div className="list">
        <h4 onClick={() => { setModal(!modal) }}>{titles[2]}</h4>
        <p>2월 17일 발행</p>
      </div> */}

      <input onChange={(e) => { 
        setInput(e.target.value);
        console.log(input)
      }}/>

      <button disabled = {!input}  onClick={() => {
        let copy = [...titles];
        let copy2 = [...like];
        copy2.unshift(0)
        copy.unshift(input);
        changeTitle(copy)
        changeLike(copy2)
        }}>글발행</button>

     {
        modal == true  ? <Modal title ={title} color={'skyblue'} titles={titles}/> : null
     }
      <Profile />
     {
        titles.map(function(a, i){
          return ( <div className="list" key={i}>
          <h4 onClick={() => { setModal(!modal); setTitle(i) }} >{titles[i]}<span onClick={ (e) => { 
            e.stopPropagation()
            let copy = [...like];
            copy[i] += 1;
            changeLike(copy)
          }}> 👍 </span> {like[i]} </h4>
          <p>2월 12일 발행</p>
          <button onClick={() => {
            let copy = [...titles];
            copy.splice(i, 1);
            changeTitle(copy);
          } }> 삭제 </button>
        </div>)
        })
     }
      
    </div>
  );
}

function Modal(props){
  return (
    <div className = "modal" style={{background : props.color}}>
        <h4>{ props.titles[props.title] }</h4>
        <p>날짜</p>
        <p>상세내용</p>
      </div>
  )
}

export default App;


class Profile extends React.Component{
  constructor(){
    super();
    this.state = { name : 'Na' , age : 26 }
  }

  changeName = () => {
    this.setState( {name: 'Park'})
  }

  render(){
    return(
      <div>
        <h3>프로필입니다</h3>
        <p> 저는 {this.state.name} 입니다. </p>
        <button onClick={ this.changeName }> 버튼 </button>
      </div>
    )
  }
}